.containerProjects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 2em 4em;
  background-color: #000;
}

.containerProjects .project{
  max-width: 600px;
  text-align: center;
  background-color: #61DAFB;
  margin: 5px;
  border:  2px solid black;
  border-radius: 10px;
}

.containerProjects .project img{
  width: 500px;
  height: 500px;
  object-fit: cover;
  margin: 5px;
}

.containerProjects .project h2{
  margin: 5px;
  font-size: 30px;
}

.containerProjects .project p{
  margin: 5px;
  font-size: 20px;
}

.title{
  margin: 0.5em auto;
  text-align: center;
  font-size: 45px;
  text-shadow: 3px 3px 3px #FFF;
  color: #000;
}

.descTitle{
  margin: 0 4.5em;
  text-align: center;
  font-size: 25px;
  color: #FFF;
  border: 2px solid black;
  padding: 0.5em;
  background-color: #282c34;

}